import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
//
import './Profit.sass'
import './dad.scss'
//
import Slider from 'rc-slider';





const Profit = () => {
    //
    const [amoutPlusSteps, setAmoutPlusSteps] = useState(50)
    const [investmentType, setInvestmentType] = useState('Silver')
    let fifty = 50

    const goo = async () => {
        let response = await fetch('https://maps.googleapis.com/maps/api/timezone/json?location=39.6034810%2C-119.6822510&timestamp=1331161200&key=YOUR_API_KEY')
        if (!response.ok) {
            throw new Error(`HTTP error! status: ${response.status}`);
          } else {
            return await console.log('dasdadasd', response);
          }
    }
    useEffect(() => { goo()} ,[])
    //
    useEffect(() => {
        if(amoutPlusSteps < 10000 && investmentType === 'Gold'){
            setInvestmentType('Silver')
        } 
    }, [amoutPlusSteps <= 10000 ])

    useEffect(() => {
        if(amoutPlusSteps > 10000 && investmentType !== 'Gold'){
            setInvestmentType('Gold')
        } 
    }, [amoutPlusSteps > 10000])

    useEffect(() => {
        if(amoutPlusSteps < 50000 && investmentType === 'Diamond'){
            setInvestmentType('Gold')
        } 
    }, [amoutPlusSteps < 50000])

    useEffect(() => {
        if(amoutPlusSteps > 50000  && investmentType === 'Gold'){
            setInvestmentType('Diamond')
        } 
    }, [amoutPlusSteps > 50000])
    //

  return (

    <div className='Profit'>
        <div className='Profit_header'>
            <h2>Profit calculation {investmentType}</h2>
            <p>Find out your total profits using the below calculator.</p>
        </div>
    <div className='Profit-w'>

        

        <div className='Profit-window'>
            <div className={`Profit-window_amount 
            ${investmentType === 'Gold' ? 'gold' : ''}
            ${investmentType === 'Silver' ? 'silver' : ''}
            ${investmentType === 'Diamond' ? 'diamond' : ''}

            `}
                
            >
                <p>Enter Investment Amount</p>
                <span>{amoutPlusSteps.toString()} $</span>
                <div className='slid'>
                    <Slider
                        className='dn'
                        max={2000}
                        min={1}
                        onChange={(e) => {
                            setAmoutPlusSteps(
                                fifty * e
                            )
                            console.log('eeeee,', e)
                        }}
                    />
                </div>
                
            </div>

            <div className='Profit-window_progress_bar'>

            </div>

            <div className='Profit-window_text'>
                <div className='flex-column w100'>
                    <span>Payout currency</span>
                    <h2>USD</h2>
                </div>
                <div className='flex-column w100'>
                    <span>Profit Days</span>
                    <h2>125</h2>
                </div>
                <div className='flex-column w100'>
                    <span>Potential Income</span>
                    <h2>{amoutPlusSteps * 1.75}</h2>
                </div>
                <div className='flex-column w100'>
                    <span>Total income</span>
                    <h2>{[
                        investmentType === 'Silver' ? 1.4 : '',
                        investmentType === 'Gold' ? 1.8 : '',
                        investmentType === 'Diamond' ? 2.3 : ''
                    ]}</h2>
                </div>
            </div>

            <Link className='btn' to='Login'>Start investing</Link>
        </div>

    </div>
    </div>
  )

}






export default Profit