import React, { useState, useEffect } from 'react'
//
import './_loginForm.scss'
//

async function loginUser(credentials) {
    return fetch('http://192.168.31.224:8081/api/auth/signin', {
        method: 'POST',
        headers: {
            'Content-Type' : 'application/json',
        },
        body: JSON.stringify(credentials)
    })
    .then(data => data.json())
}


let LoginForm = ({ loginWindow, setLoginWindow }) => {

    const [username, setUserName] = useState();
    const [password, setPassword] = useState();

    const [data, setData] = useState()
    const [token, setToken] = useState()

    const [loginStatus, setLoginStatusError] = useState()

    const [test, setTest] = useState()
    const [postId, setPostId] = useState()


    const handleSubmit = async e => {
        e.preventDefault();
        const token = await loginUser({
            username,
            password
        });
        setData(token);
    }    


    useEffect(() => {

        if(data?.accessToken ){
            fetch('http://192.168.31.224:8081/api/test/user', {
                method: 'get',
                headers: {
                    'x-access-token': data?.accessToken
                }
            })
            .then((response) => response.json())
            .then(data => console.log('trrrrttttt', data))

            // .then(data => setTest(data))
        }
    
    }, [data?.accessToken]);


    useEffect(() => {
        setTimeout(() => setLoginStatusError(null), 1000);
    }, [loginStatus])

    useEffect(() => {
        if(data?.accessToken !== (null) && data?.accessToken !== (undefined)){
            setTimeout(() => setLoginWindow(!loginWindow), 1000);
        }
    }, [data?.accessToken])


    return(
        <div className='background_auth'>
            <div className="background">
                <div className="shape"></div>
                <div className="shape"></div>
            </div>
            <form onSubmit={(username && password) ? handleSubmit : null}>

                <div className='pb-20 flex-row flex-jcsb flex-align-items-center'>
                    <h3>Login</h3>

                    <div onClick={() => setLoginWindow(!loginWindow)} 
                        className={`close ${data?.accessToken ? 'bgGreen' : '' }`}>
                        <span></span>
                        <span></span>
                        <span></span>
                        <span></span>
                        <svg viewBox="0 0 36 36" className="circle">
                            <path
                                stroke-dasharray="100, 100"
                                d="M18 2.0845
                                    a 15.9155 15.9155 0 0 1 0 31.831
                                    a 15.9155 15.9155 0 0 1 0 -31.831"
                            />
                        </svg>
                    </div>
                </div>

                {/* Email */}
                <input type="text" placeholder="Email or Phone" 
                    onChange={e => setUserName(e.target.value)} id="username" />
                {data?.message === "User Not found." 
                    ? <p>User Not found.</p> 
                    : null
                }
                {/* Password */}
                <input type="password" placeholder="Password" id="password" 
                    onChange={e => setPassword(e.target.value)} />
                {data?.message === "Invalid Password!" 
                    ? <p>Invalid Password</p> 
                    : null
                }

                <button>Log In</button>

            </form>
        </div>
    )
}




export default LoginForm