import React from 'react'
import { Link, useLocation } from "react-router-dom"; 
//
import './Navigation.sass'




const Navigation = () => {

    const { pathname } = useLocation();

    return (
        <div className='nav'>

            <nav>
                <ul className="nav-menu">
                    <li className="nav-item"> 
                        <Link to="/markets" className={`nav-link ${pathname === '/markets' ? 'active' : ''}`}>Markets</Link> 
                    </li>
                    <li className="nav-item"> 
                        <Link to="/dashboard" className={`nav-link ${pathname === '/dashboard' ? 'active' : ''}`}>Dashboard</Link> 
                    </li>
                    <li className="nav-item"> 
                        <Link to="/deposit" className={`nav-link ${pathname === '/deposit' ? 'active' : ''}`}>Deposit</Link> 
                    </li>
                    <li className="nav-item"> 
                        <Link to="/borrow" className={`nav-link ${pathname === '/borrow' ? 'active' : ''}`}>Borrow</Link> 
                    </li>
                </ul>
            </nav>

        </div>
    )
}

export default Navigation